@import url(https://fonts.googleapis.com/css?family=Alegreya+Sans&display=swap);
:root {
  --bumblebee: #ffff8c;
  --sombra: #3c5369;
}
body {
  font-family: "Alegreya Sans", sans-serif;
  text-align: center;
  background-color: #3c5369;
  background-color: var(--sombra);
}
.nav {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  background-color: #3c5369;
  background-color: var(--sombra);
  position: fixed;
  top: 0;
  z-index: 1;
}
a {
  text-decoration: none;
  color: black;
}

.menu-link {
  background-color: slategrey;
  border-radius: 5px;
  padding: 5px;
  margin-bottom: 1px;
  border: 2px white solid;
  width: 26vw;
}
#posts-list {
  width: 27vw;
}

.non-dropdown {
  margin: 1%;
}

.dropdown {
  position: relative;
  display: inline-block;
  margin: 1%;
  border-radius: 5px;
}

.hide {
  display: none;
  position: absolute;
  background-color: #ffff8c;
  background-color: var(--bumblebee);
  width: 200px;
  border-radius: 5px;
}

.menu-link:hover {
  color: #ffff8c;
  color: var(--bumblebee);
  background-color: #3c5369;
  background-color: var(--sombra);
}
.show {
  display: flex;
  position: absolute;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ffff8c;
  background-color: var(--bumblebee);
  width: 200px;
  border-radius: 5px;
}

.show div {
  width: 27vw;
  background-color: white;
  text-align: center;
  line-height: 200%;
  margin: 1px;
  color: black;
  border-radius: 3px;
}

.show div:hover {
  background-color: #ffff8c;
  background-color: var(--bumblebee);
}

.page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.title, #banner {
  color: #ffff8c;
  color: var(--bumblebee);
}

.title {
  -webkit-text-decoration: underline white;
          text-decoration: underline white;
}

#banner {
  margin-bottom: 5%;
  font-weight: strong;
  font-size: 300%;
}
.postTile {
  border: 2px white solid;
  border-radius: 5px;
  margin-bottom: 3%;
  padding: 3%;
}

figcaption {
  font-style: italic;
  text-align: center;
  margin-top: 3%;
  color: white;
}

.img-center {
  margin: 5% auto;
  max-width: 50%;
  display: block;
  border: black solid 2px;
}

p, ul {
  max-width: 50%;
  line-height: 200%;
  color: white;
}

ul {
  text-align: left;
}

.text-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
